import React from 'react'
import './SubGallery.scss';
import { Link } from 'react-router-dom';
import simpleParallax from 'simple-parallax-js'

import img1 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC01.jpg';
import img2 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC02.jpg';
import img3 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC03.jpg';
import img4 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC04.jpg';
import img5 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC05.jpg';
import img6 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC06.jpg';
import img7 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC07.jpg';
import img8 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC08.jpg';
import img9 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC09.jpg';
import img10 from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 1080x720/DSC10.jpg';

import img1S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_01.jpg';
import img2S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_02.jpg';
import img3S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_03.jpg';
import img4S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_04.jpg';
import img5S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_05.jpg';
import img6S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_06.jpg';
import img7S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_07.jpg';
import img8S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_08.jpg';
import img9S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_09.jpg';
import img10S from '../img/Rodzinne i dziecięce/2018.06.16- Świerklaniec 600x400/DSC_10.jpg';

const imgSet1 = [
    { path: img1, pathS: img1S, },
    { path: img2, pathS: img2S, },
    { path: img3, pathS: img3S, },
    { path: img4, pathS: img4S, },
    { path: img5, pathS: img5S, },
    { path: img6, pathS: img6S, },
    { path: img7, pathS: img7S, },
    { path: img8, pathS: img8S, },
    { path: img9, pathS: img9S, },
    { path: img10, pathS: img10S, },
]

const photos = imgSet1.map(item => {
    return (
        <div className="SubGallery__photo" key={item.path}>
            <Link to={`/${item.name}`}>
                <picture>
                    <source media="(min-width: 461px)" srcSet={item.path} />
                    <img src={item.pathS} className="d-block w-100" alt="..." />
                </picture>
            </Link>
        </div >
    )
})

class Swierklaniec extends React.Component {

    componentDidMount = () => {
        const image = document.getElementsByClassName('thumbnail');
        new simpleParallax(image, {
            scale: 1.7,
            delay: 1,
        });
    }

    render() {
        return (
            <div className="SubGallery">
                <div className="SubGallery__photos">
                    {photos}
                </div>
            </div>
        )
    }
}

export default Swierklaniec
